import React from 'react'
// import PropTypes from 'prop-types'
import { Form, Input, Modal, Select } from 'antd'
import config from '../../../utils/config'

const { reasonType } = config

const FormItem = Form.Item
const { Option } = Select
const { TextArea } = Input
const formItemLayout = {
  labelCol: {
    span: 6,
  },
  wrapperCol: {
    span: 14,
  },
}
interface Iprops {
  item: any,
  onOk: any,
  form: any,
  [propsname: string]: any
}

const modal = ({
  item = {},
  onOk,
  form: {
    getFieldDecorator,
    validateFields,
    getFieldsValue,
  },
  ...modalProps
}: Iprops) => {
  const handleOk = () => {
    validateFields((errors: any) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        id: item.id,
      }
      onOk(data)
    })
  }

  const modalOpts = {
    ...modalProps,
    onOk: handleOk,
  }
  return (
    <Modal {...modalOpts}>
      <Form layout="horizontal">
        <FormItem label="下架原因" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('reason_type', {
            rules: [
              {
                required: true,
                message: '请选择下架原因',
              },
            ],
          })(<Select
            showSearch={true}
            placeholder="请选择原因"
            optionFilterProp="children"
            filterOption={(input: string, option: any) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            {reasonType.map((data, index) => <Option key={index} value={index + 1}>{data}</Option>)}
          </Select>)}
        </FormItem>
        <FormItem label="备注" hasFeedback={true} {...formItemLayout}>
          {getFieldDecorator('remark', {
            // initialValue: item.day,
            rules: [
              {
                // required: true,
              },
            ],
          })(<TextArea autosize={true} />)}
        </FormItem>
      </Form>
    </Modal>
  )
}

// modal.propTypes = {
//   form: PropTypes.object.isRequired,
//   type: PropTypes.string,
//   item: PropTypes.object,
//   onOk: PropTypes.func,
// }

export default Form.create()(modal)



