import React, { Fragment } from 'react'
// import PropTypes from 'prop-types'
import { connect } from 'dva'
import { routerRedux } from 'dva/router'
import { Row, Col, Input, Table, Button, Tabs, Modal } from 'antd'
import queryString from 'query-string'
import config from '../../utils/config'
import OffShelfModel from './Modals/OffShelfModel'


import style from './works.less'

const { TabPane } = Tabs
// import style from './songlists.less'
const { Search } = Input
const { reasonType } = config
const EnumPostStatus = {
  target_type2: 2,
  target_type3: 3,
}

interface Iprops {
  reportcontent: any,
  loading: any,
  dispatch: any,
  location: any,
}

class ReportContent extends React.PureComponent<Iprops> {
  public showModal = (item: string) => {
    this.props.dispatch({
      type: 'reportcontent/updateState',
      payload: {
        modalVisible: true,
        currentItem: item,
      },
    })
  }
  public delComment = (item: any) => {
    const { dispatch } = this.props
    Modal.confirm({
      title: '删除',
      content: '删除后不可恢复，确定要删除吗？',
      onOk () {
        dispatch({
          type: 'reportcontent/delComment',
          payload: {
            id: item.id,
          },
        })
      },
      onCancel () {console.log(1)},
    })
  }
  public handleSearch = (val: string) => {
    this.props.dispatch({
      type: 'reportcontent/updateState',
      payload: {
        searchValue: val,
      },
    })
    this.props.dispatch({
      type: 'reportcontent/query',
      payload: {},
    })
  }
  public handleTabClick = (key: number) => {
    const { pathname } = this.props.location
    this.props.dispatch(routerRedux.push({
      pathname,
      search: queryString.stringify({
        target_type: key,
      }),
    }))
  }
  // getStatus = (type) => {

  // }
  public render () {
    const {
      reportcontent, dispatch, loading, location,
    } = this.props
    const {
      list, pagination, currentItem, modalVisible,
    } = reportcontent
    location.query = queryString.parse(location.search)
    const { query, pathname } = location
    const columns = [
      {
        title: '序号',
        dataIndex: '',
        render (text: string, record: any, index: number) {
          // TODO
          return index + 1
        },
      },
      {
        title: '作品',
        dataIndex: '',
        render: (data: any) => <span className={style.avatar}><img src={data.work_comment.work.description} alt="封面" /></span>,
      },
      {
        title: '配文',
        dataIndex: '',
        render: (data: any) => <span>{data.work_comment.work.description}</span>,
      },
      {
        title: '用户',
        dataIndex: '',
        render: (data: any) => <span>{data.work_comment.user.name}</span>,
      },
      {
        title: '发布时间',
        dataIndex: 'created_at',
        sorter: (a: any, b: any) => {
          return new Date(a.created_at).getTime() - new Date(b.created_at).getTime()
        },
      },
      {
        title: '举报人',
        dataIndex: '',
        render: (data: any) => <span>{data.user.name}</span>,
      },
      {
        title: '原因',
        dataIndex: 'reason_type',
        render: (item: any) => <span>{reasonType[item - 1]}</span>,
      },
      {
        title: '备注',
        dataIndex: 'remark',
      },
      {
        title: '举报时间',
        dataIndex: 'updated_at',
        sorter: (a: any, b: any) => {
          return new Date(a.updated_at).getTime() - new Date(b.updated_at).getTime()
        },
      },
      {
        title: '操作',
        render: (val: string) => <Button onClick={() => this.showModal(val)}>下架</Button>,
      },
    ]
    const commentColumns = [
      {
        title: '序号',
        dataIndex: '',
        render (text: any, record: any, index: number) {
          // TODO
          return index + 1
        },
      },
      {
        title: '主题作品',
        dataIndex: '',
        render: (data: any) => <span className={style.avatar}><img src={data.work_comment.work.description} alt="封面" /></span>,
      },
      {
        title: '主题配文',
        dataIndex: '',
        render: (data: any) => <span>{data.work_comment.work.description}</span>,
      },
      {
        title: '评论详情',
        dataIndex: '',
        render: (data: any) => <span>{data.work_comment.content}</span>,
      },
      {
        title: '用户',
        dataIndex: '',
        render: (data: any) => <span>{data.work_comment.user.name}</span>,
      },
      {
        title: '发布时间',
        dataIndex: 'created_at',
        sorter: (a: any, b: any) => {
          return new Date(a.created_at).getTime() - new Date(b.created_at).getTime()
        },
      },
      {
        title: '举报人',
        dataIndex: 'user',
        render: (item: any) => <span>{item.name}</span>,
      },
      {
        title: '原因',
        dataIndex: 'reason_type',
        render: (item: number) => <span>{reasonType[item - 1]}</span>,
      },
      {
        title: '备注',
        dataIndex: 'remark',
      },
      {
        title: '举报时间',
        dataIndex: 'updated_at',
        sorter: (a: any, b: any) => {
          return new Date(a.updated_at).getTime() - new Date(b.updated_at).getTime()
        },
      },
      {
        title: '操作',
        render: (val: any) => <Button onClick={() => this.delComment(val)}>删除</Button>,
      },
    ]
    const listProps = {
      pagination,
      dataSource: list,
      columns: query.target_type === 2 ? columns : commentColumns,
      loading: loading.effects['reportcontent/query'],
      onChange (page: any) {
        dispatch(routerRedux.push({
          pathname,
          search: queryString.stringify({
            ...query,
            current_page: page.current,
            per_page: page.pageSize,
          }),
        }))
      },
    }
    const modalProps = {
      item: currentItem,
      visible: modalVisible,
      maskClosable: false,
      // confirmLoading: loading.effects['user/update'],
      title: '下架',
      wrapClassName: 'vertical-center-modal',
      onOk (data: any) {
        dispatch({
          type: 'reportcontent/offShelf',
          payload: {
            // modalVisible: false,
            status: 3,
            ...data,
          },
        })
        dispatch({
          type: 'reportcontent/updateState',
          payload: {
            modalVisible: false,
          },
        })
      },
      onCancel () {
        dispatch({
          type: 'reportcontent/updateState',
          payload: {
            modalVisible: false,
          },
        })
      },
    }


    return (
      <Fragment>
        <Row className="margin-bottom" type="flex" align="middle">
          <Col span={6}>
            <h3 style={{ marginBottom: 0 }}>举报内容</h3>
          </Col>
          <Col span={18} className={style.textRight}>
            <Search style={{ width: 300 }} placeholder="请输入要搜索的作品名称" onSearch={this.handleSearch} enterButton={true} />
          </Col>
        </Row>
        <Tabs activeKey={query.target_type === String(EnumPostStatus.target_type3) ? String(EnumPostStatus.target_type3) : String(EnumPostStatus.target_type2)} onTabClick={this.handleTabClick}>
          <TabPane tab="举报的作品" key={String(EnumPostStatus.target_type2)}>
            <Table
              {...listProps}
              bordered={true}
              // scroll={{ x: 1200 }}
              // columns={columns}
              rowKey={record => record.id}
            />
          </TabPane>
          <TabPane tab="举报的评论" key={String(EnumPostStatus.target_type3)}>
            <Table
              {...listProps}
              bordered={true}
              // scroll={{ x: 1200 }}
              // columns={columns}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>

        {modalVisible && <OffShelfModel {...modalProps} />}
      </Fragment>
    )
  }
}
export default connect(({
  reportcontent, loading, dispatch,
}: Iprops) => ({
  reportcontent, loading, dispatch,
}))(ReportContent)



